@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=PT+Sans&display=swap');

#root {
     height: 100%;
     position: relative;
}

body{
     text-align: center;
     background-color: gray;
     height: 100%;
     margin: 0;
}
.heading{
     font-family: 'Montserrat', sans-serif;
     color: white;
     font-size: 4rem;
     margin: 0 0 2rem 0;
     padding: 2rem 0;
     background-color: black;
     border-bottom: 2px solid white;
}

input {
     max-width: 40%;
}

h3 {
     font-size: 2.5rem;
     text-decoration: underline;
}

h4 {
     font-family: "Montserrat", sans-serif;
}

.cardContainer{
     color: black;
     font-family: "Montserrat", sans-serif;
     display: grid;
     grid-template-columns: 1fr 1fr;
     gap: 2rem;
     width: 100%;
     height: auto;
     margin-bottom: 2rem;
     padding: 20px 0;
     text-align: center;
     justify-items: center;
}
.bigCard1{
     width: 90%;
     background-color: lightgreen;
     padding: 30px 0;
     border-radius: 20px;
     border: 2px solid black;
     box-shadow: 3px 3px 3px darkgrey;
}

.item {
     text-align: center;
     display: flex;
     flex-direction: column;
     align-items: center;
}

.item p{
     display: inline;
     padding-right: 5px;
     font-size: 1.5rem;
}
.item input {
     text-align: left;
     font-size: 1.5rem;
     border-radius: 10px;
}
.add-button {
     font-size: 1.5rem;
     padding: 0 7px;
     border-radius: 50%;
     margin-left: 5px;
     border: 2px solid white;
     background-color: gray;
     color: white;
     transition: all .3s ease;
}

.add-button:hover{
     background-color: rgb(75, 75, 75);
     cursor: pointer;
}

.addedPrice{
     display: block;
     margin-right: 10%;
}

.listItem{
     display: inline-block;
     list-style: none;
     font-family: "PT Sans", sans-serif;
     font-size: 1.5rem;
     margin: 0 1rem 1rem 0;
}

#totalButton{
     font-family: "Montserrat", sans-serif;
     font-size: 1.3rem;
     background-color: black;
     color: white;
     display: block;
     text-align: center;
     margin: 2rem auto;
     padding: .5rem 1.25rem;
     box-shadow: 3px 3px 3px darkgrey;
     border-radius: 20px;
     transition: all .3s ease;
}
#totalButton:hover{
     background-color: white;
     color: black;
     cursor: pointer;
}
#deleteButton {
     display: inline-block;
     background-color: rgb(249, 130, 130);
     min-height: 2rem;
     width: 2rem;
     font-size: 1.5rem;
     color: white;
     border: 2px solid white;
     border-radius: 50%;
     padding: 0 7px;
     transition: all .3s ease;
}
#deleteButton:hover {
     background-color: rgb(247, 52, 52);
     cursor: pointer;
}
.balanceSection {
     font-family: "Montserrat", sans-serif;;
     width: 50%;
     color: white;
     display: inline-block;
     margin-bottom: 10rem;
     background-color: black;
     text-align: center;
     padding: 30px 0;
     border-radius: 20px;
     border: 2px white solid;
     box-shadow: 3px 3px 3px darkgrey;
}

.balanceSection h2 {
     font-size: 4rem;
     text-decoration: none;
     color: lightgreen;
     text-shadow: 1px 1px black;
}

#balance-button {
     padding: .5rem 3rem;
     font-family: "Montserrat", sans-serif;
     font-size: 1.5rem;
     border-radius: 20px;
     color: lightgreen;
     border: 2px white solid;
     background-color: black;
     transition: all .3s ease;
}

#balance-button:hover {
     cursor: pointer;
     color: black;
     background-color: lightgreen;
}

.balance-savings-item {
     text-decoration: none;
}

.footer{
     background-color: black;
     position: absolute;
     bottom: 0;
     width: 100%;
     color: darkgray;
     border-top: 2px solid white;
}

@media all and (max-width: 400px) {
     .cardContainer {
          display: block;
     }
}